:root {
  --font-main: Josefin Sans, sans-serif;
  --font-secondary: Lexend Deca, sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --darkdesaturatedblue: hsl(244, 38%, 16%);
  --verydarkblue: hsl(233, 47%, 7%);
  --slightlytransparentwhite1: hsla(0, 0%, 100%, 0.75);
  --slightlytransparentwhite2: hsla(0, 4%, 20%, 0.6);
  --softviolet: hsl(277, 64%, 61%);
  --darkgrayishred: hsl(0, 6%, 24%);
  --des-red: hsl(234, 31%, 56%);
  --soft-red: hsl(0, 93%, 68%);
  --green: hsl(120, 88%, 36%);
  --gradient1: linear-gradient(135deg, from hsl(0, 0%, 100%), to hsl(0, 29%, 95%));
  --gradient2: linear-gradient(135deg, from hsl(0, 80%, 86%), to hsl(0, 74%, 74%));
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: var(--verydarkblue) !important;
  padding: 0;
  height: 100vh;
  display: flex;
  position: absolute;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translateX(-50%);
  text-align: center !important;
  font-size: 0.9375rem !important;
}
@media (min-width: 64em) {
  body {
    padding: 0;
    height: 100vh;
    display: flex !important;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 39.25rem !important;
  margin: 0 auto;
}
@media (min-width: 64em) {
  .container {
    padding: 0 1.5rem;
    max-width: 68.75rem !important;
    margin: 0 auto;
  }
}

.s-card {
  border-radius: 0.9375rem;
  background-image: linear-gradient(135deg, white, #fff0f0);
}
.s-card__text {
  padding: 0rem 0.625rem 0rem 0.625rem;
}
@media (max-width: 26.66em) {
  .s-card__text {
    padding: 0rem 1.875rem 0rem 1.875rem;
  }
}
.s-card__socials {
  display: flex;
  justify-content: space-evenly;
  padding: 0;
  padding-bottom: 1.25rem;
  list-style: none;
}
.s-card__socials .fab {
  color: var(--des-red);
}
.s-card__email {
  padding-bottom: 2.5rem;
}
.s-card--img {
  max-width: 100%;
  height: auto;
  border-radius: 0.625rem;
}
.s-card--header {
  text-transform: uppercase;
  font-size: 2.1875rem;
  font-weight: 400;
  letter-spacing: 8px;
  font-family: var(--font-main);
  color: var(--black);
}
.s-card--text {
  padding: 0.625rem 0rem 0.625rem 0rem;
  font-family: var(--font-main);
  color: var(--slightlytransparentwhite2);
}
.s-card span {
  display: block;
  color: var(--des-red);
}
.s-card--btn {
  height: 40px;
  width: 55px;
  border-radius: 25px;
  text-align: center;
  margin-left: -55px;
  border: none;
  background: var(--soft-red);
  background: -webkit-linear-gradient(135deg, #595959, #d6d6d6);
}
.s-card input {
  font-size: 0.75rem;
  border-radius: 25px;
  border: solid 1px var(--des-red);
  width: 250px;
  height: 40px;
  padding-left: 15px;
}
.s-card input::placeholder {
  color: var(--des-red);
}
.s-card small {
  display: block;
  position: absolute;
  visibility: hidden;
  width: 90%;
  letter-spacing: 0.0625rem;
  color: var(--green);
}
.s-card .form-controller.success small {
  visibility: visible;
}

@media (min-width: 64em) {
  .s-card {
    background-image: linear-gradient(135deg, white, #fff0f0);
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "content image";
  }
  .s-card__img {
    grid-area: image;
  }
  .s-card__text {
    padding: 4.375rem 4.375rem 0.9375rem 3.4375rem;
  }
}
@media (min-width: 64em) and (min-width: 71.875em) {
  .s-card__text {
    padding: 4.375rem 4.375rem 2.5rem 3.4375rem;
  }
}
@media (min-width: 64em) {
  .s-card__socials {
    display: flex;
    justify-content: space-evenly;
    padding: 0;
    padding-bottom: 1.25rem;
    list-style: none;
  }
  .s-card__socials .list-item {
    max-height: 15px;
  }
}
@media (min-width: 64em) {
  .s-card--img {
    max-width: 100%;
    height: 28.75rem;
    padding-left: 0.625rem !important;
    padding-bottom: 0;
  }
}
@media (min-width: 64em) and (min-width: 71.875em) {
  .s-card--img {
    height: 27.5rem;
  }
}
@media (min-width: 64em) {
  .s-card--header {
    font-size: 1.875rem;
    margin-bottom: 1.125rem;
  }
}
@media (min-width: 64em) and (min-width: 71.875em) {
  .s-card--header {
    font-size: 2.1875rem;
  }
}
@media (min-width: 64em) {
  .s-card--text {
    padding: 0.625rem 0.3125rem 0rem 0rem;
  }
}
@media (min-width: 64em) and (min-width: 71.875em) {
  .s-card--text {
    padding: 1.875rem 0.3125rem 0rem 0rem;
    font-size: 1.0625rem;
  }
}
@media (min-width: 64em) {
  .s-card input {
    width: 280px;
  }
}